import React, { useState, useContext } from 'react';
import '../App.css';
import { CartContext } from '../App';
import { ProductInfos } from './products';


const CartComp = ({ product, addToCart, removeFromCart, forms, setForms }) => {
    const { cart } = useContext(CartContext);

    const [newName, setNewName] = useState("");
    const [newEmail, setNewEmail] = useState("");
    const [newPhone, setNewPhone] = useState("");
    const [newAddress, setNewAddress] = useState("");


    const prices = cart.map(l => l.amount * l.price)
    const itemsPrice = prices.reduce((a, b) => a + b, 0);
    /*const taxPrice = itemsPrice * 0.14;
    const shippingPrice = itemsPrice > 2000 ? 0 : 20;
    const totalPrice = itemsPrice + taxPrice + shippingPrice;*/
    console.log(itemsPrice);


    return (
        <div>
            {cart.map(product => <div><p>{product.name} {product.amount} x {product.price}€ </p>
                <button className="add" onClick={() => addToCart({name : product.name, amount: 1, id: product.id, price:product.price})}>+</button>
               
                <button className="remove" onClick={() => removeFromCart({name : product.name, amount: 1, id: product.id, price:product.price})}>-</button>
        
        </div>)}
        Yhteensä: {itemsPrice}€
        <br></br><hr></hr><br></br>
            <div className="Form">
                <form onSubmit={e => {
                    e.preventDefault();
                    setForms(forms.concat({nimi: newName, email: newEmail, phone: newPhone, address: newAddress}));
                    setNewName("");
                    setNewEmail("");
                    setNewPhone("");
                    setNewAddress("");
                    alert(`Kiitos tilauksesta ${newName}. Tuote lähetetään osoitteeseen ${newAddress}. Kuitti lähetetään sähköpostiinne ${newEmail}. Tuotteiden hinnat ovat yhteensä ${itemsPrice}€`);
                    window.location.reload(false);
                }}>
                    <label for="name">Nimi: </label>
                    <input type="text" value={newName} onChange={e => setNewName(e.target.value)} required></input><br></br>
                    
                    <label for="email">Sähköposti: </label>
                    <input type="text" value={newEmail} onChange={e => setNewEmail(e.target.value)} required></input><br></br>

                    <label for="phone">Puhelinnumero: </label>
                    <input type="text" value={newPhone} onChange={e => setNewPhone(e.target.value)} required></input><br></br>

                    <label for="adress">Osoite: </label>
                    <input type="text" value={newAddress} onChange={e => setNewAddress(e.target.value)} required></input><br></br>

                    <button className="confirm" type="submit" value="submit">Vahvista Tilaus</button>
                </form>

            </div>


        </div>

    )
}

export default CartComp;