import React, {useState} from 'react';
import './App.css';
import {createContext} from 'react';
import productsData from './components/productsData';
import {ProductInfos} from './components/products';
import CartComp from './components/Cart';

export const CartContext = createContext ()

function App() {
  const [cart, setCart] = useState([]);
  const [page, setPage] = useState(1);
  const [forms, setForms] = useState([]);
  console.log(cart);
  console.log(page);
  

  function addToCart(product) {
    let found = null 
    const tempCart= cart.map(productP => {
        if(productP.id == product.id){
            found = true
            return {...productP, amount: productP.amount + 1}
        } else {
            return productP;
            
        }})
        if (!found) {
            setCart(cart.concat(product));
        } else {
            setCart(tempCart);
        }
    }

function removeFromCart(product) {
    let found = null 
    const tempCart= cart.map(productP => {
        if(productP.id == product.id){
            found = true
            if(productP.amount > 0) {
            return {...productP, amount: productP.amount - 1}
            }
            else {
              return productP;
            }
        } else {
            return productP;
                
        }})
        if (!found) {
            setCart(cart.concat(product));
        } else {
            setCart(tempCart);
        }
    }
  return (
    <CartContext.Provider value={{cart: cart, setCart: setCart}}>
    <div className="App">
    
      <header id="navbar">
        <nav class="navbar-container container">
          <a href="/" class="home-link">
            <div class="navbar-logo"></div>
            Website Name
          </a>
          <button type="button" id="navbar-toggle" aria-controls="navbar-menu" aria-label="Toggle menu" aria-expanded="false">
            <span class="icon-bar"></span>
            <span class="icon-bar"></span>
            <span class="icon-bar"></span>
          </button>
          
          <div id="navbar-menu" aria-labelledby="navbar-toggle">
            <ul class="navbar-links">
              <li class="navbar-item"><a class="navbar-link" href="/about">About</a></li>
              <li class="navbar-item"><a class="navbar-link" href="/blog">Blog</a></li>
              <li class="navbar-item"><a class="navbar-link" href="/careers">Careers</a></li>
              <li class="navbar-item"><a class="navbar-link" href="/contact">Contact</a></li>
            </ul>
          </div>
        </nav>
      </header>
      <header className="App-header">
        <h1>Fanikauppa</h1>

      </header>
      <div className="products">
        {page == 1 &&  
        <ProductInfos products={productsData} addToCart={addToCart} removeFromCart={removeFromCart} setPage={setPage}/>}

        {page == 2 &&
        <CartComp products={productsData} addToCart={addToCart} removeFromCart={removeFromCart} setPage={setPage} setForms={setForms} forms={forms} />}

      </div>
    </div>
    </CartContext.Provider>
  );
  
}

export default App;
