import React, { useState, useContext } from 'react';
import '../App.css';
import {CartContext,} from '../App';

const ProductInfo = ({product, addToCart, removeFromCart}) => {
    const {setCart, cart} = useContext(CartContext);
    const [showInfo, setShowInfo] = useState(false);
    const [clicked, setClicked] = useState(false);
    return (
        <div className = "product">
            <img src={product.img}></img>
            <h2 onClick={()=>setShowInfo(!showInfo)}>{product.name}</h2>
                {showInfo &&
            <div className = "productdesc">
                <p>{product.desc}</p>
                <p>{product.price}€</p>
                <button className={clicked ? "AddTo clicked" : "AddTo"} onClick={() => {setClicked(true);  addToCart({name : product.name, amount: 1, id: product.id, price:product.price})}}>Add To Cart </button>
                {product.amount}
            </div>}
        </div>
    )
}




const ProductInfos = ({products, setPage, addToCart, removeFromCart}) => {
    
    return (

    
    <div className = "products">
        {products.map(s => <ProductInfo key={s.id} product={s} addToCart={addToCart} removeFromCart={removeFromCart}/>)}
        <button className="cart" onClick={() => setPage(2)}>Ostoskori</button>
    </div>
    )
}

export {ProductInfo, ProductInfos};